.root{
    --primary-color: #000000;
}

.card{
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 250px;
    height: 250px;
    border: 2px solid rgb(226, 160, 16);
    padding: 0.5rem 0.3rem;
    margin: 13px 10px;
    position: relative;
    border-radius: 10px;
}

.card__title{
    margin: 0px;
    font-weight: 4000;
    text-align: center;
    justify-content: space-between;
}

.card__price{
    margin: 10px;
    font-weight: bold;
    justify-content: space-between;
}

.ton-logo{
    position: absolute;
    padding: 2px 5px;
    width: 7%;
}

.image__container{
    width: 150px;
    height: 150px;
    margin: 0px auto;
}

.image__container img{
    padding: 0px 0px;
    width: 100%;
    object-fit: cover;
}

.btn-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.card__price{
    text-align: center;
    display: inline-block;
    justify-content: center;
}

.card__badge{
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background-color: rgb(227, 191, 30);
    border-radius: 50%;
    transform: translate(20px, -15px) scale(1);
    color: #fff;
    font-weight: bold;
    text-align: center;
    border: 1px solid rgb(255, 255, 255);
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: scaleanimation 1.5s ease infinite;
    opacity: 0.9;
}

@keyframes scaleanimation {
    to{
        transform: translate(20px, -15px) scale(1.05);
        opacity: 1;
    }
}

.card__badge--hidden{
    display: none;
}